const TubeGauge = ({ value, min, max, width, height, darkMode }) => {
  const percentage = ((value - min) / (max - min)) * 100;
  const chartColor = darkMode ? "#ffffff" : "#000000";
  const fillColor = darkMode ? "#8884d8" : "#82ca9d";

  return (
    <svg width={width} height={height} viewBox="0 0 100 300">
      {/* Tube */}
      <rect x="30" y="10" width="40" height="280" fill="none" stroke={chartColor} strokeWidth="2" rx="20" ry="20" />
      
      {/* Fill */}
      <rect x="30" y={290 - (percentage * 2.8)} width="40" height={percentage * 2.8} fill={fillColor} rx="20" ry="20" />
      
      {/* Min and Max labels */}
      <text x="80" y="290" fill={chartColor} fontSize="12">{min}</text>
      <text x="80" y="20" fill={chartColor} fontSize="12">{max}</text>
      
      {/* Value label */}
      <text x="50" y="150" fill={chartColor} fontSize="14" textAnchor="middle">{value}</text>
    </svg>
  );
};

