const GanttChart = ({ data, height, width, darkMode }) => {
  const chartColor = darkMode ? "#ffffff" : "#000000";

  return (
    <ResponsiveContainer width={width} height={height}>
      <BarChart
        layout="vertical"
        data={data}
        margin={{ top: 20, right: 30, left: 40, bottom: 5 }}
      >
        <CartesianGrid strokeDasharray="3 3" stroke={chartColor} />
        <XAxis type="number" stroke={chartColor} />
        <YAxis dataKey="task" type="category" stroke={chartColor} />
        <Tooltip />
        <Bar dataKey="duration" fill="#8884d8" shape={<Rectangle radius={[0, 10, 10, 0]} />}>
          <LabelList dataKey="task" position="insideLeft" fill={darkMode ? "#000" : "#fff"} />
        </Bar>
      </BarChart>
    </ResponsiveContainer>
  );
};

const BeeswarmChart = ({ data, width, height, darkMode }) => {
  const chartColor = darkMode ? "#ffffff" : "#000000";

  return (
    <ResponsiveContainer width={width} height={height}>
      <ScatterChart margin={{ top: 20, right: 20, bottom: 20, left: 20 }}>
        <XAxis type="number" dataKey="value" name="value" stroke={chartColor} />
        <YAxis type="number" dataKey="index" name="index" stroke={chartColor} hide />
        <Tooltip cursor={{ strokeDasharray: '3 3' }} />
        <Scatter data={data.map((item, index) => ({ ...item, index }))} fill="#8884d8" />
      </ScatterChart>
    </ResponsiveContainer>
  );
};

