const App = () => {
  const [currentSection, setCurrentSection] = useState('home');
  const [darkMode, setDarkMode] = useState(false);

  useEffect(() => {
    document.title = `ZeroTruth | ${currentSection.charAt(0).toUpperCase() + currentSection.slice(1)}`;
  }, [currentSection]);

  const renderSection = () => {
    switch (currentSection) {
      case 'work':
        return <Work darkMode={darkMode} />;
      case 'about':
        return <About />;
      case 'contact':
        return <Contact />;
      default:
        return <Home />;
    }
  };

  return (
    <div className={`min-h-screen flex flex-col ${darkMode ? 'bg-gray-900 text-white' : 'bg-white text-black'}`}>
      <Navbar currentSection={currentSection} setCurrentSection={setCurrentSection} />
      <main className="flex-grow pt-16">
        {renderSection()}
      </main>
      <Footer />
      <button
        onClick={() => setDarkMode(!darkMode)}
        className="fixed bottom-4 right-4 bg-blue-600 text-white p-2 rounded-full"
      >
        {darkMode ? '' : ''}
      </button>
    </div>
  );
};

export default App;
