import React, { useEffect, useRef } from 'react';
import * as d3 from 'd3';
import { feature } from 'topojson-client';
import worldData from './world-110m.json';  // You'll need to download this file

const InteractiveGlobe = ({ onClose }) => {
  const svgRef = useRef();

  useEffect(() => {
    const width = 800;
    const height = 600;

    const svg = d3.select(svgRef.current)
      .attr('width', width)
      .attr('height', height);

    const projection = d3.geoOrthographic()
      .scale(250)
      .translate([width / 2, height / 2]);

    const path = d3.geoPath().projection(projection);

    const globe = svg.append('g');

    const countries = feature(worldData, worldData.objects.countries).features;

    globe.selectAll('path')
      .data(countries)
      .enter().append('path')
      .attr('d', path)
      .attr('fill', '#69b3a2')
      .attr('stroke', '#fff');

    // Rotation
    let rotation = 0;
    d3.timer(() => {
      projection.rotate([rotation, 0]);
      globe.selectAll('path').attr('d', path);
      rotation += 0.2;
    });

    // Tooltip
    const tooltip = d3.select('body').append('div')
      .attr('class', 'tooltip')
      .style('position', 'absolute')
      .style('visibility', 'hidden')
      .style('background-color', 'white')
      .style('border', 'solid')
      .style('border-width', '1px')
      .style('border-radius', '5px')
      .style('padding', '10px');

    globe.selectAll('path')
      .on('mouseover', (event, d) => {
        tooltip.style('visibility', 'visible')
          .html(`${d.properties.name}<br>First film: 1895`)  // Replace with actual data
          .style('left', (event.pageX + 10) + 'px')
          .style('top', (event.pageY - 28) + 'px');
      })
      .on('mouseout', () => {
        tooltip.style('visibility', 'hidden');
      });
  }, []);

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex justify-center items-center z-50">
      <div className="bg-white p-8 rounded-lg">
        <button onClick={onClose} className="float-right">Close</button>
        <svg ref={svgRef}></svg>
      </div>
    </div>
  );
};

export default InteractiveGlobe;